<!-- 服务包详情 -->
<template>
   <div class="servicePackage-detail-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isCustom='false'></Breadcrumb>
    </div>
    <div class="inner-box">
       <div class="detail-box" v-if="servicePackageInfo">
         <el-row>
           <el-col :span="9">
              <div class="item-box">
                <div class="item-label">服务包类型：</div>
                <div class="item-content">{{servicePackageInfo.packageType?packageTypeOption[servicePackageInfo.packageType]:EMPTYTEXT}}</div>
              </div>
           </el-col>
           <el-col :span="9">
             <div class="item-box">
                <div class="item-label">服务包名称：</div>
                <div class="item-content">{{servicePackageInfo.packageName ||EMPTYTEXT}}</div>
              </div>
           </el-col>
         </el-row>
         <el-row>
           <el-col :span="9">
              <div class="item-box">
                <div class="item-label">服务包年龄限制：</div>
                <div class="item-content">{{servicePackageInfo.limitAgeMin?(servicePackageInfo.limitAgeMin+'-'+servicePackageInfo.limitAgeMax+'岁'):EMPTYTEXT}}</div>
              </div>
           </el-col>
           <el-col :span="9">
             <div class="item-box">
                <div class="item-label">服务包性别限制：</div>
                <div class="item-content">{{servicePackageInfo.limitSexType||servicePackageInfo.limitSexType==0?sexTypeOption[servicePackageInfo.limitSexType]:EMPTYTEXT}}</div>
              </div>
           </el-col>
         </el-row>
          <el-row>
           <el-col :span="9">
             <div class="item-box">
                <div class="item-label">服务时长：</div>
                <div class="item-content">{{servicePackageInfo.serviceDays?servicePackageInfo.serviceDays+'天':EMPTYTEXT}}</div>
              </div>
           </el-col>
           <el-col :span="9">
              <div class="item-box">
                <div class="item-label">服务包状态：</div>
                <div class="item-content">{{servicePackageInfo.packageStatus?serviceStatusOption[servicePackageInfo.packageStatus]:EMPTYTEXT}}</div>
              </div>
           </el-col>
         </el-row>
         <el-row v-if="servicePackageInfo.packageType===1">
           <el-col :span="9">
              <div class="item-box">
                <div class="item-label">服务包价格：</div>
                <div class="item-content">{{servicePackageInfo.originalPrice||EMPTYTEXT}}</div>
              </div>
           </el-col>
           <el-col :span="9">
              <div class="item-box">
                <div class="item-label">优惠价格：</div>
                <div class="item-content">{{servicePackageInfo.discountPrice||EMPTYTEXT}}</div>
              </div>
           </el-col>
         </el-row>
          <div class="item-box">
            <div class="item-label">服务包标签：</div>
            <div class="item-content">{{servicePackageInfo.labelNameList||EMPTYTEXT}}</div>
          </div>
           <div class="item-box">
            <div class="item-label">服务包项目信息：</div>
            <div class="item-content tsItem">
               <template v-if="servicePackageInfo.itemsJson.length>0">
                  <table class="tables-box" cellspacing="0" cellspadding="0">
                  <thead>
                    <th>项目名称</th>
                    <th>项目详情</th>
                    <th>履约类型</th>
                    <th>履约内容</th>
                    <th>履约格式</th>
                    <th>履约次数</th>
                    <th>状态</th>
                  </thead>
                  <tbody v-for="(item,index) in servicePackageInfo.itemsJson" :key="index">
                      <tr v-if="index>0">
                        <td colspan="7" class="title">项目{{index+1}}</td>
                      </tr>
                      <tr>
                        <td>{{item.itemName||EMPTYTEXT}}</td>
                        <td>{{item.itemDetail||EMPTYTEXT}}</td>
                        <td>{{item.appointType?appointTypeOption[item.appointType]:EMPTYTEXT}}</td>
                        <td>{{item.appointDetail?appointDetailOption[item.appointDetail]:EMPTYTEXT}}</td>
                        <template v-if="item.appointType!==16">
                          <td>{{item.appointFormat?appointFormatOption[item.appointFormat]:EMPTYTEXT}}</td>
                          <td>{{item.appointNum||EMPTYTEXT}}</td>
                          <td>{{item.itemStatus?statusOption[item.itemStatus]:EMPTYTEXT}}</td>
                        </template>
                        <template v-else>
                          <td>{{EMPTYTEXT}}</td>
                          <td>{{EMPTYTEXT}}</td>
                          <td>{{EMPTYTEXT}}</td>
                        </template>
                      </tr>
                      <tr v-for="(it,i) in item.childrens" :key="i">
                        <td colspan="7" class="subTable">
                          <table cellspacing="0" cellspadding="0">
                            <tbody>
                              <tr>
                                <td colspan="7" class="title">执行项{{i+1}}</td>
                              </tr>
                              <tr>
                                <td>{{it.itemName||EMPTYTEXT}}</td>
                                <td>{{it.itemDetail||EMPTYTEXT}}</td>
                                <td>{{it.appointType?appointTypeOption[it.appointType]:EMPTYTEXT}}</td>
                                <td>{{it.appointDetail?appointDetailOption[it.appointDetail]:EMPTYTEXT}}</td>
                                <td>{{it.appointFormat?appointFormatOption[it.appointFormat]:EMPTYTEXT}}</td>
                                <td>{{it.appointNum||EMPTYTEXT}}</td>
                                <td>{{it.itemStatus?statusOption[it.itemStatus]:EMPTYTEXT}}</td>
                              </tr>
                            </tbody>
                          </table>
                        </td>
                      </tr>
                  </tbody>
                </table>
               </template>
              <template v-else>暂无服务包项目</template>
            </div>
          </div>
       </div>
    </div>
   </div>
</template>

<script>
export default {
    name:'servicePackage-detail',
    data() {
      return {
        servicePackageInfo:null, //服务包信息
        sexTypeOption:{
          0:'不限制',
          1:'男',
          2:'女'
        }, //性别限制
        serviceStatusOption:{
          1:'已启用',
          2:'已禁用'
        }, //服务包状态
        packageTypeOption:{
          1:'付费',
          2:'免费'
        },
        appointTypeOption:{
          1:'测量类',
          2:'标记类',
          4:'公共卫生类',
          8:"指导类",
          16:"组合类"
        },//履约类型
        appointDetailOption:{
          1001:'心电',
          1002:'血氧',
          1003:'血压',
          1004:"尿常规",
          1005:"体温",
          1006:'血糖',
          1007:'白细胞',
          1008:'血红蛋白',
          1009:"糖化血红蛋白",
          1010:"血脂",
          1011:'身高',
          1012:'体重',
          1013:'尿酸',
          2001:'健康档案',
          2002:'健康体检',
          2003:'预防接种',
          2004:"儿童",
          2005:"孕产妇",
          2006:'老年人',
          2007:'高血压',
          2008:'糖尿病',
          2009:"严重精神障碍",
          2010:"肺结核",
          2011:'健康教育',
          2012:'信息报告',
          2013:'巡查登记',
          2014:"传染病报告",
          2015:"突发公共卫生事件"
        },//履约内容
        appointFormatOption:{
          1:'年',
          2:'季度',
          4:'月',
          8:'日'
        },//履约格式
        statusOption:{
          1:'是',
          2:'否',
        }, //状态
      };
    },
    created() {
      this.getservicePackageDetail() //获取服务包详情
    },
    methods: {
      //获取服务包详情
      getservicePackageDetail(){
        this.$http.get(process.env.VUE_APP_API+'/family/servicePackage/getDtoByPackageId',{
          packageId:this.$route.query.id
        }).then(res => {
          if(res.code===200){
            let result = res.data
            this.servicePackageInfo = result
            this.servicePackageInfo.itemsJson = result.itemsJson?JSON.parse(result.itemsJson):[] // 服务包项目
          }else{
            this.$message.error(res.msg);
          }
        })
        .catch(error => {
          this.$message.error(error);
        });
      }
    }
};
</script>

<style lang="scss">
@import "./tables";
.servicePackage-detail-box{
  .inner-box{
    padding:30px 70px;
    .detail-box{
      .item-box{
        line-height:30px;
        font-size: 14px;
        color: rgba(0,0,0,0.85);
        display:flex;
        margin-bottom:10px;
        .item-label{
         width:130px;
         text-align:right;
         padding-right:12px;
        }
        .item-content{
          flex:1;
          overflow: hidden; 
          text-overflow: ellipsis; 
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          &.tsItem{
            display:initial;
          }
          @include tables-box();
        }
      }
    }
  }
}
</style>
